<template>
  <div class="container">
    <el-row type="flex" class="row-bg" justify="center">
      <span class="title">请输入自助口令</span>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="4">
        <span class="svg-container">
          <svg-icon icon-class="password"></svg-icon>
        </span>
        <el-input name="password" :type="pwdType" @keyup.enter.native="handleLogin" placeholder="password"></el-input>
        <span class="show-pwd" @click="showPwd"><svg-icon icon-class="eye" /></span>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="16">
      </el-col>
      <el-col :span="8">
        <router-link :to="{name: 'reserved'}">
          <el-button type="primary" class="btn_large">返回</el-button>
        </router-link>
        <router-link :to="{name: 'order'}">
          <el-button type="primary" class="btn_large">下一步</el-button>
        </router-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pwdType: 'password'
    };
  },
  methods: {
    showPwd() {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },
    handleLogin() {}
  }
};
</script>

<style scoped>
.title {
  font-size: 32px;
}

.el-row {
  margin-top: 20px;
}

.el-button {
  margin-left: 20px;
}

.btn_large {
  margin-top: 20px;
  font-size: 32px;
}
</style>



